import EditSectionComponent from '../../components/EditSectionComponent';
import StepsComponent from '../../components/StepsComponent'
import HeaderComponent from '../../components/HeaderComponent';

import DefaultTemplate from '../../templates/DefaultTemplate/DefaultTemplate'
import { get_root_url } from '../../../utils/utils'
import EditRightTab from '../../components/EditRightTab'

import {useEffect} from 'react';


export default function EditSectionPage(props){
	
  const {tglScreen, tipScrn} = props;

	return (

			<>
				<HeaderComponent />
		        <section className="edit-sec">

	            	<div className="container-fluid">

	                	<div className="row">
	                    	<div className="col-lg-6 redit-area">
	                      		<StepsComponent step={'2'} />
	                      		<EditSectionComponent/>
	                      	</div>

		                 	<div className="col-sm-6 prev-bx">

                          <div className="preview-container">
                            <DefaultTemplate/>  
                          </div>

                          <div className="tipBackgorund d-none">
                            <div className="topTips">
                             <img src={`${get_root_url}/assets/images/tipssec.svg`} alt="" />

                              <h3>
                                Pro tip for an eye catching resume
                              </h3>


                              <ul>
                                <li>
                                  For US, UK, or Canada it is strongly recommended NOT to add a photo. 
                                </li>
                                <li>
                                  Standard European email format based on the following: firstname.lastname@company.com
                                </li>
                                <li>
                                  Recuiters are most likley will search your Linkedin profile, Add it. 
                                </li>
                                <li>
                                  Photo - Make sure to show the head and shoulders only, don't use filters (It's not Instagram)
                                </li>
                              </ul>

                              <button className='btn btn-cstm'>
                                Got it
                              </button>

                            </div>
                          </div>

                          <div className="edit-block">
                            <EditRightTab/>
                          </div>
                        
                      </div>
		                </div>

		            </div>
		          </section>
			</> 
		)
}