import { set } from 'js-cookie';
import React, { useRef, useState } from 'react'
import { get_root_url } from '../../utils/utils';
import { faCheck, faTimes, faInfoCircle, faChevronDown } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export default function EditSectionComponent() {

    const editorRef = useRef(null);
    const log = () => {
        if (editorRef.current) {
            console.log(editorRef.current.getContent());
        }
    };

    const [EndDate, setEndDate] = useState('');

    const focusAndValid = e => {
        let label = e.target.nextElementSibling;
        console.log(label);
        label.classList.add('to-top');
    }

    const blurAndValid = e => {

        if (EndDate.trim().replace("\\n", '') != '') return;


        let label = e.target.nextElementSibling;
        console.log(label);
        label.classList.remove('to-top');
    }

    const handleEndDate = e => {
        let value = e.target.value;
        setEndDate(value);
    }


    return (
        <>
            <div className="profile-info-bx edit-section">
                <h3>
                    Edit Sections 
                </h3>

                <div className="row">

                    <div className="col-md-12">
                        <span className='edit-section-box'>
                            Contact Information
                        </span>
                    </div>

                    <div className="col-md-12">
                        <span className='edit-section-box'>
                            Experience
                        </span>
                    </div>

                    <div className="col-md-12">
                        <span className='edit-section-box'>
                            Education
                        </span>
                    </div>

                    <div className="col-md-12">
                        <span className='edit-section-box'>
                            Skillsand Language
                        </span>
                    </div>

                </div>

                <div className="profile-form dwload-shre-btn">

                    <div className="row btm-btn-sec justify-content-center">

                        <div className="col-sm-7 btn-mt">
                            <div className="btn-group d-block">
                                <button type="button" className="btn dropdown-toggle btn-cstm dropdown-toggle d-flex justify-content-center align-items-center" data-bs-toggle="dropdown" aria-expanded="false">
                                    Download Resume
                                    
                                    <svg xmlns="http://www.w3.org/2000/svg" width="10" height="6" viewBox="0 0 10 6" fill="none">
                                        <path d="M1 1L4.66667 5L9 1" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" />
                                    </svg>
                                </button>
                                <ul className="dropdown-menu">
                                    <li><a className="dropdown-item" href="#">Download DOC</a></li>
                                    <li><a className="dropdown-item" href="#">Download PDF</a></li>
                                    <li><a className="dropdown-item" href="#">Download TXT</a></li>
                                   
                                </ul>
                            </div>
                        </div>


                        <div className="col-sm-7">
                            <button className='btn bk-edits-btn'>
                                <img src={`${get_root_url}/assets/images/Share.svg`} alt="" /> Share Online
                            </button>
                        </div>

                    </div>

                </div>


            </div>

        </>
    )
}
